import Select from 'react-select'

import { useState, useEffect } from 'react';
import img from "../../Constants/Admin/images";
import { getRequest, putRequest } from '../../api/request';
import UserLayout from '../../layouts/User/UserLayout'
import { toast } from 'react-toastify';
import { Carousel, Image, Container, Row, Col } from "react-bootstrap";
import {
    Button,
    FormGroup,
    Label
} from "reactstrap";
import { AvForm, AvField } from 'availity-reactstrap-validation';
import PhoneInput from 'react-phone-input-2';
import { Link } from 'react-router-dom';

const MemberEditProfile = () => {
    const [memberProfile, setMemberProfile] = useState();
    const [parties, setParties] = useState()
    const [changeParty, setChangeParty] = useState()
    const [changeAge, setChangeAge] = useState()
    const [changeProfession, setChangeProfession] = useState()
    const ageOptions = [
        { value: '15', label: '15' },
        { value: '16', label: '16' },
        { value: '17', label: '17' },
        { value: '18', label: '18' },
        { value: '19', label: '19' },
        { value: '20', label: '20' },
        { value: '21', label: '21' },
        { value: '22', label: '22' },
        { value: '23', label: '23' },
        { value: '24', label: '24' },
        { value: '25', label: '25' },
        { value: '26', label: '26' },
        { value: '27', label: '27' },
        { value: '28', label: '28' },
        { value: '29', label: '29' },
        { value: '30', label: '30' },
        { value: '31', label: '31' },
        { value: '32', label: '32' },
        { value: '33', label: '33' },
        { value: '34', label: '34' },
        { value: '35', label: '35' },
        { value: '36', label: '36' },
        { value: '37', label: '37' },
        { value: '38', label: '38' },
        { value: '39', label: '39' },
        { value: '40', label: '40' },
        { value: '41', label: '41' },
        { value: '42', label: '42' },
        { value: '43', label: '43' },
        { value: '44', label: '44' },
        { value: '45', label: '45' },
        { value: '46', label: '46' },
        { value: '47', label: '47' },
        { value: '48', label: '48' },
        { value: '49', label: '49' },
        { value: '50', label: '50' },
        { value: '51', label: '51' },
        { value: '52', label: '52' },
        { value: '53', label: '53' },
        { value: '54', label: '54' },
        { value: '55', label: '55' },
        { value: '56', label: '56' },
        { value: '57', label: '57' },
        { value: '58', label: '58' },
        { value: '59', label: '59' },
        { value: '60', label: '60' },
        { value: '61', label: '61' },
        { value: '62', label: '62' },
        { value: '63', label: '63' },
        { value: '64', label: '64' },
        { value: '65', label: '65' },
        { value: '66', label: '66' },
        { value: '67', label: '67' },
        { value: '68', label: '68' },
        { value: '69', label: '69' },
        { value: '70', label: '70' },
        { value: '71', label: '71' },
        { value: '72', label: '72' },
        { value: '73', label: '73' },
        { value: '74', label: '74' },
        { value: '75', label: '75' },
        { value: '76', label: '76' },
        { value: '77', label: '77' },
        { value: '78', label: '78' },
        { value: '79', label: '79' },
        { value: '80', label: '80' },
        { value: '81', label: '81' },
        { value: '82', label: '82' },
        { value: '83', label: '83' },
        { value: '84', label: '84' },
        { value: '85', label: '85' },
        { value: '86', label: '86' },
        { value: '87', label: '87' },
        { value: '88', label: '88' },
        { value: '89', label: '89' },
        { value: '90', label: '90' },
        { value: '91', label: '91' },
        { value: '92', label: '92' },
        { value: '93', label: '93' },
        { value: '94', label: '94' },
        { value: '95', label: '95' },
        { value: '96', label: '96' },
        { value: '97', label: '97' },
        { value: '98', label: '98' },
        { value: '99', label: '99' },
        { value: '100', label: '100' },
    ]
    const professionOptions = [
        { value: 'Self Employed', label: 'Self Employed' },
        { value: 'Government', label: 'Government' }
    ]
    const getProfile = async () => {
        try {
            const token = localStorage.getItem("TOKEN");
            const response = await getRequest(
                `/api/secure/user/profile`,
                token
            );
            console.log("response.result.data.user", response.result.data.user);
            setMemberProfile(response.result.data.user);
        } catch (error) {
            console.log("Get Member Profile error", error);
        }
    }
    const getParties = async () => {
        try {
            const token = localStorage.getItem("TOKEN");
            const response = await getRequest(
                `/api/secure/party/party-list`,
                token
            );
            console.log("response", response.result.data.parties);
            setParties(response.result.data.parties);
        } catch (error) {
            console.log("Get Site Setting Error", error);
        }
    };
    const defaultParty = async () => {
        try {
            const token = localStorage.getItem("TOKEN");
            const response = await getRequest(
                `/api/secure/party/party-list`,
                token
            );
            console.log('memberProfile.partyId',memberProfile.partyId)
            return response.result.data.parties.filter(async (e) => {
                if (e._id === memberProfile.partyId) {
                    return e.title
                }
            })
        } catch (error) {
            console.log("Get Site Setting Error", error);
        }
    }
    const ageChange = (selectedOption) => {
        setChangeAge(selectedOption);
    }
    const professionChange = (selectedOption) => {
        setChangeProfession(selectedOption);
    }
    const partiesChange = (selectedOption) => {
        setChangeParty(selectedOption);
    }
    const memberProfileUpdateHandler = async (e) => {
        try {
            e.preventDefault();
            const formData = new FormData();
            formData.append(
                "firstName",
                e.target[0].value
            );
            formData.append(
                "middleName",
                e.target[1].value
            );
            formData.append(
                "lastName",
                e.target[2].value
            );
            formData.append(
                "email",
                e.target[3].value
            );
            formData.append(
                "phone",
                e.target[4].value
            );
            if (changeAge) {
                formData.append(
                    "age",
                    changeAge.value
                );
            }
            formData.append(
                "address",
                e.target[6].value
            );
            formData.append(
                "fbLink",
                e.target[7].value
            );
            if (changeProfession) {
                formData.append(
                    "profession",
                    changeProfession.value
                );
            }
            if (changeParty) {
                formData.append(
                    "partyId",
                    changeParty.value
                );
            }
            formData.append(
                "userId",
                localStorage.getItem("ROLE")
            );
            const APIresponse = {
                firstName: formData.get('firstName'),
                middleName: formData.get('middleName'),
                lastName: formData.get('lastName'),
                email: formData.get('email'),
                phone: formData.get('phone').replace(/-|\s/g, ""),
                age: formData.get('age'),
                address: formData.get('address'),
                fbLink: formData.get('fbLink'),
                profession: formData.get('profession'),
                partyId: formData.get('partyId'),
                userId: localStorage.getItem("USER_ID"),
            };
            console.log("APIresponse", APIresponse)
            const token = localStorage.getItem("TOKEN");
            const response = await putRequest(
                "/api/secure/user/edit-profile",
                token,
                APIresponse,
            );

            console.log("status", response);
            if (response.result.status === 200) {
                localStorage.setItem(
                    "USER_FIRSTNAME",
                    APIresponse.firstName
                );
                localStorage.setItem(
                    "USER_LASTNAME",
                    APIresponse.lastName
                );
                localStorage.setItem(
                    "USER_EMAIL",
                    APIresponse.email
                );
                toast.success('Profile Updated', {
                    position: "bottom-right",
                    autoClose: 2000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: false,
                    draggable: true,
                    progress: undefined,
                });
            }

        } catch (error) {
            console.log("Set Profile APi error", error.message);
        }
    }
    useEffect(() => {
        getProfile();
        getParties();

    }, []);
    return (
        <UserLayout>
            <main id="main_content">
                <section className="latest_posts  bg-white v2">
                    <div className="container">
                        <div className="row justify-content-center">
                            <div className="col-lg-10 col-md-10 col-12">
                                <h1 className="text-center">Edit Profile</h1>
                                <div className="row justify-content-center">
                                    <div className="col-lg-6 col-md-8 col-12">
                                        <div className="form_box">
                                            {
                                                (memberProfile) ? <AvForm className="form" encType="multipart/form-data" method="post" onValidSubmit={memberProfileUpdateHandler}>
                                                    <FormGroup className="mb-4">
                                                        <Label>First Name</Label>

                                                        <AvField
                                                            name="firstName"
                                                            type="text"
                                                            placeholder="First Name"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.firstName}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Middle Name</Label>

                                                        <AvField
                                                            name="middleName"
                                                            type="text"
                                                            placeholder="Middle Name"
                                                            className="form-control"
                                                            value={memberProfile.middleName}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Last Name</Label>

                                                        <AvField
                                                            name="lastName"
                                                            type="text"
                                                            placeholder="Last Name"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.lastName}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Email</Label>

                                                        <AvField
                                                            name="email"
                                                            type="email"
                                                            placeholder="Email"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.email}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Phone Number</Label>
                                                        <AvField
                                                            name="phone"
                                                            type="text"
                                                            placeholder="Phone"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.phone}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Age</Label>
                                                        <Select
                                                            onChange={ageChange}
                                                            defaultValue={{ value: true, label: memberProfile.age }}
                                                            options={ageOptions} />
                                                    </FormGroup>
                                                    <FormGroup>
                                                        <Label>Address</Label>

                                                        <AvField
                                                            name="address"
                                                            type="text"
                                                            placeholder="Address"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.address}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup>
                                                        <Label>Facebook Link</Label>

                                                        <AvField
                                                            name="fbLink"
                                                            type="url"
                                                            placeholder="Facebook Link"
                                                            className="form-control"
                                                            required
                                                            value={memberProfile.fbLink}
                                                        />

                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Professional / Field of Experties</Label>
                                                        <Select
                                                            onChange={professionChange}
                                                            options={professionOptions}
                                                            defaultValue={{ value: true, label: memberProfile.profession }}
                                                        />
                                                    </FormGroup>
                                                    <FormGroup className="mb-4">
                                                        <Label>Member Organization</Label>
                                                        {/* {
                                                            (parties) ? <Select
                                                                onChange={partiesChange}
                                                                defaultValue={{ value: true, label: defaultParty()[0].title }}
                                                                options={
                                                                    parties.map((e, index) => {
                                                                        return { value: e._id, label: e.title }
                                                                    })
                                                                } /> : ''
                                                        } */}
                                                        {
                                                            (parties) ? <Select
                                                                onChange={partiesChange}
                                                                defaultValue={{ value: true, label: defaultParty() }}
                                                                options={parties.map((e, index) => {
                                                                    return { value: e._id, label: `${e.title}` }
                                                                })
                                                                } /> : ''
                                                        }

                                                    </FormGroup>
                                                    <div className="text-center">
                                                        <Button className="mt-4 btn-default" color="primary" type="submit">
                                                            Update
                                                        </Button>
                                                    </div>
                                                </AvForm> : ""
                                            }


                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row mt-4" >
                            <div className="col text-center">
                                <Link to="/account" className="btn ">
                                    Back
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        </UserLayout>
    )
}

export default MemberEditProfile
